.Burger {
    width: 90%;
    padding: 0;
    margin: 1rem auto;
    height: 250px;
    overflow: auto;
    text-align: center;
    font-weight: bold;
    font-size: 1.2rem;
}

@media(min-width: 1000px) and (min-height: 700px) {
    .Burger {
        width: 80%;
        height: 600px;
    }
}

@media(min-width: 500px) and (min-height: 401px) {
    .Burger {
        width: 80%;
        height: 400px;
    }
}

@media(min-width: 500px) and (min-height: 400px) {
    .Burger {
        width: 500px;
        height: 300px;
    }
}